import { toast } from "react-toastify";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createProduct } from "../../actions/productAction";
import FileUpload from "../../components/Admin/FileUpload";

import AdminNavs from "../../components/Admin/AdminNavs";

const initialState = {
  name: "",
  description: "",
  price: 0,
  shipping: "",
  countInStock: "",
  category: "",
  images: [],
  brands: "",
  color: "",
  givePay: "",
  totalPay: "",
  supplier: "",
};

const CreateProducts = ({ history }) => {
  const [values, setValues] = useState(initialState);
  const [isShow, setisShow] = useState(false);
  const [categoryName, setcategoryName] = useState("");
  const [supplierName, setsupplierName] = useState("");
  const [brandName, setbrandName] = useState("");
  const user = useSelector((state) => state.user);
  const [uploading, setUploading] = useState(false);
  const dispatch = useDispatch();
  const { success, error } = useSelector((state) => state.createProduct);
  const {
    name,
    description,
    price,
    category,
    shipping,
    countInStock,
    images,
    brands,
    color,
    givePay,
    totalPay,
    supplier,
  } = values;
  if (category === "other") {
    values.categoryName = categoryName;
  }
  if (supplier === "other") {
    values.supplierName = supplierName;
  }
  if (brands === "other") {
    values.brandName = brandName;
  }

  const handleSubmit = (e) => {
    // e.preventDefault();
    if (
      name === "" ||
      countInStock === 0 ||
      category === "" ||
      description === "" ||
      brands === ""
    ) {
      toast.error("Please all of the requirements");
    } else if (price.length <= 0) {
      toast.error("Price must be greater then Zero");
    }
    // else if (totalPay > givePay || totalPay === givePay) {
    //     toast.error("Total Pay must be equal or less then GivenPay")
    // } else {
    else {
      
      dispatch(createProduct(values));
      history.push("/admin/products");
      toast.success("product created");
      setValues(initialState);
    }
  };

  const handleChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value });
  };

  return (
    <>
      <AdminNavs />
      <FileUpload
        values={values}
        setValues={setValues}
        setUploading={setUploading}
      />

      {error && toast.error(`${error}`)}
      {uploading && <p>Loading...</p>}
      <section class="text-gray-600 body-font">
        <div class="container px-5 py-10 mx-auto flex flex-wrap items-center bg-gray-100">
          {/* left box */}
          <div class="lg:w-1/2 md:w-1/2 bg-gray-100 rounded-lg p-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0">
            <h2 class="text-gray-900 text-lg font-medium title-font mb-5">
              Fill the all feilds*
            </h2>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Product Name
              </label>
              <input
                type="text"
                id=" Product Name"
                name="name"
                required
                value={name}
                onChange={handleChange}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Description
              </label>
              <input
                type="text"
                id=" description"
                name="description"
                required
                value={description}
                onChange={handleChange}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Category
              </label>
              <select
                name="category"
                id="category"
                onChange={handleChange}
                required
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              >
                <option>Please select</option>
                <option value="laptops">Laptops</option>
                <option value="headphones">Headphones</option>
                <option value="smarttv">Smart TV</option>
                <option value="mobiles">Mobiles</option>
                <option value="accessories">Accessories</option>
                <option value="tripods">Tripods</option>
                <option value="other">other</option>
              </select>
            </div>

            {category === "other" && (
              <div class="relative mb-4">
                <label for="full-name" class="leading-7 text-sm text-gray-600">
                  Category Name
                </label>
                <input
                  type="text"
                  name="categoryName"
                  className="form-control"
                  onChange={(e) => setcategoryName(e.target.value)}
                  id=" description"
                  required
                  class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                />
              </div>
            )}

            <button
              onClick={handleSubmit}
              class="text-white bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg"
            >
              Save
            </button>
            <p class="text-xs text-gray-500 mt-3">
              Literally you probably haven't heard of them jean shorts.
            </p>
          </div>

          {/* right box */}
          <div class="lg:w-1/2 md:w-1/2 bg-gray-100 rounded-lg p-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0">
            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Price
              </label>
              <input
                type="number"
                name="price"
                required
                value={price}
                onChange={handleChange}
                id=" description"
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>
            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Shipping
              </label>
              <select
                name="shipping"
                id="shipping"
                onChange={handleChange}
                required
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              >
                <option>Please select</option>
                <option value="No">No</option>
                <option value="Yes">Yes</option>
              </select>
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Count in stock
              </label>
              <input
                type="number"
                name="countInStock"
                value={countInStock}
                onChange={handleChange}
                required
                id=" description"
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Brand
              </label>
              <select
                name="brands"
                onChange={handleChange}
                required
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              >
                <option>Please select</option>
                <option value="New">Apple</option>
                <option value="samsung">Samsung</option>
                <option value="tripod">Tripod</option>
                <option value="dell">Dell</option>
                <option value="hp">HP</option>
                <option value="orient">Orient</option>
                <option value="dawnlance">Dawnlance</option>
                <option value="radoWatch">Rado Watch</option>
                <option value="canon">Canon</option>
                <option value="revs">Revs</option>
                <option value="other">others</option>
              </select>
            </div>

            {brands === "other" && (
              <div class="relative mb-4">
                <label for="full-name" class="leading-7 text-sm text-gray-600">
                  Brand Name
                </label>
                <input
                  type="text"
                  name="supplierName"
                  value={brandName}
                  onChange={(e) => setbrandName(e.target.value)}
                  class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                />
              </div>
            )}

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Supplier
              </label>
              <select
                name="supplier"
                onChange={handleChange}
                required
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              >
                <option>Supplier</option>
                <option value="laptopsAgent">Laptop Suppliers</option>
                <option value="mobilesAgent">Mobile Suppliers</option>
                <option value="accessoriesAgent">Accessories Suppliers</option>
                <option value="camerasAgent">Cameras Suppliers</option>
                <option value="watchesAgent">Watch Suppliers</option>
                <option value="tvAgent">televission Suppliers</option>
                <option value="headphonesAgent">Headphones Suppliers</option>
                <option value="other">other</option>
              </select>
            </div>

            {supplier === "other" && (
              <div class="relative mb-4">
                <label for="full-name" class="leading-7 text-sm text-gray-600">
                  Supplier Name
                </label>
                <input
                  type="text"
                  name="supplierName"
                  value={supplierName}
                  onChange={(e) => setsupplierName(e.target.value)}
                  class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                />
              </div>
            )}

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Total Payment
              </label>
              <input
                required
                type="number"
                name="totalPay"
                value={totalPay}
                onChange={handleChange}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Given Payment
              </label>
              <input
                type="number"
                name="givePay"
                required
                value={givePay}
                onChange={handleChange}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default CreateProducts;
