import axios from "axios";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Alert } from "react-bootstrap";
import { useSelector } from "react-redux";
import Resizer from "react-image-file-resizer";
import { BiTrash, BiUpload } from "react-icons/bi";
import { API } from "../../config/API";

const FileUpload = ({ values, setValues, setUploading }) => {
  const [im, setIm] = useState("");
  const user = useSelector((s) => s.user);
  const [sizeOk, setSizeOk] = useState(false);
  const [isShow, setisShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [isShow2, setisShow2] = useState(true);

  const fileUploadAndResize = (e) => {
    setLoading(true);
    let files = e.target.files;
    let allUploadedFiles = values.images;

    if (files) {
      setUploading(true);
      for (let i = 0; i < files.length; i++) {
        Resizer.imageFileResizer(
          files[i],
          300,
          300,
          "JPEG",
          100,
          0,
          (uri) => {
            axios
              .post(
                `${API}/uploadimages`,
                { image: uri },
                {
                  headers: {
                    authtoken: user ? user.token : "",
                  },
                }
              )
              .then((res) => {
                setUploading(false);
                allUploadedFiles.push(res.data);
                setLoading(false);
                setValues({ ...values, images: allUploadedFiles });
              })
              .catch((err) => {
                setUploading(false);
                toast.error(`${err.message}`);
                setSizeOk(true);
                setisShow(true);
                setLoading(false);
              });
          },
          "base64"
        );
      }
    }
  };

  const handleRemove = (public_id) => {
    setUploading(true);
    setLoading(true);
    axios
      .put(
        `${API}/removeimage`,
        { public_id },
        {
          headers: {
            authtoken: user ? user.token : "",
          },
        }
      )
      .then((res) => {
        setUploading(false);
        setLoading(false);
        const { images } = values;
        let filteredImages = images.filter((x) => x.public_id !== public_id);
        setValues({ ...values, images: filteredImages });
      })
      .catch((err) => {
        setUploading(false);
        setLoading(false);

        console.error(err);
      });
  };

  useEffect(() => {
    toast.info("Tap in image to remove that image !");
  }, []);

  return (
    <>
      <section className="text-gray-600 body-font">
        <div className="container px-5 mx-auto">
          <div className="flex items-center lg:w-3/5 mx-auto border-b pb-2 mb-2 border-gray-200 sm:flex-row flex-col">
            {/* values.images && values.images */}
            {values.images &&
              values.images.map((x, index) => (
                <div
                  style={{ cursor: "pointer" }}
                  onClick={() => handleRemove(x.public_id)}
                  key={index}
                  className="sm:w-32 sm:h-32 h-20 w-20 sm:mr-10 inline-flex items-center justify-center rounded-full bg-indigo-100 text-indigo-500 flex-shrink-0"
                >
                  <img src={x.url} className="rounded-full" />
                </div>
              ))}
          </div>
          <div class="relative mb-4">
            <label for="full-name" class="leading-7 text-sm text-gray-600">
              Upload Image {loading && <p>loading...</p>}
            </label>
            <input
              type="file"
              accept="images/*"
              multiple
              onChange={fileUploadAndResize}
            />
          </div>
          <p>Dimensions of image must be under 300x300</p>
        </div>
      </section>
    </>
  );
};

export default FileUpload;
